import React, { memo } from "react";

import styled from "styled-components";
import * as colors from "../style/colors";

const FooterContainer = styled.footer`
  /* background-color: #f065; */
  background-color: ${colors.BLACK};
  text-align: center;
  padding: 50px 10px;

  .to-the-top {
    // block을 주면 한줄 다 차지해서 width 값을 따로 줘야하는데
    // 버튼 css 적용시 width/height보다는 padding 값으로 잡아주고 폰트크기 컨트롤하는게 좋다.
    display: inline-block; 
    background-color: ${colors.LIGHT_GRAY};
    padding: 10px 25px;
    font-size: 18px;
    color: ${colors.GRAY};
    margin-bottom: 10px;

    i {
      margin-right: 10px;
    }

    &:hover {
      background-color: ${colors.GREEN};
      color: ${colors.BLACK};
    }
  }

  /* .social-list css */
  .social-list {
    display: flex;
    justify-content: center; // 가운데 정렬

    a {
      color: ${colors.WHITE};
      display: block; // 밑으로 내려가지 않는이유 >> 부모가 flex를 갖고있기 때문에
      font-size: 35px;
      margin: 20px 10px 40px 10px; // a태그에 display:block을 줘야 상하margin, padding 값을 줄 수 있다.

      &:hover {
        color: ${colors.GREEN};
      }
    }
  }

  /* address css */
  address {
    color: ${colors.WHITE};
    font-size: 18px;
  }
`;

const Footer = memo(() => {
  return (
    <FooterContainer>
      <a href="#" className="to-the-top">
        <i className="fa-sharp fa-solid fa-arrow-up"></i>To The Top
      </a>
      <ul className="social-list">
        <li>
          <a href="#">
            <i className="fa-brands fa-square-facebook"></i>
          </a>
        </li>
        <li>
          <a href="#">
            <i className="fa-brands fa-square-instagram"></i>
          </a>
        </li>
        <li>
          <a href="#">
            <i className="fa-brands fa-square-snapchat"></i>
          </a>
        </li>
        <li>
          <a href="#">
            <i className="fa-brands fa-square-pinterest"></i>
          </a>
        </li>
        <li>
          <a href="#">
            <i className="fa-brands fa-square-twitter"></i>
          </a>
        </li>
        <li>
          <a href="#">
            <i className="fa-brands fa-linkedin"></i>
          </a>
        </li>
      </ul>
      <address>copyright&copy;megait</address>
    </FooterContainer>
  );
});

Footer.defaultProps = {};

export default Footer;
