/**
 * 전역으로 적용될 기본 스타일
 * - 공통 CSS 
 */

// 패키지 참조
import { createGlobalStyle } from "styled-components";
import reset from "styled-reset";
import * as colors from './colors';

import mq from "./MediaQuery";

/** 전역 스타일 시트를 정의한 객체 */
const GlobalStyles = createGlobalStyle`
    ${reset}

    // 이렇게 정의한다면?
    // 다른 글꼴 적용할때 !important 적용시켜야한다.
    *:not(i) {
        font-family: 'Noto Sans KR' !important;
        box-sizing: border-box;
        font-size: 16px;
    }

    body {
        padding: 0;
        margin: 0;
    }

    a {
        text-decoration: none;
        color: ${colors.BLACK};
    }

    .title {
        width: 100%;
        height: 40vh;
        display: flex;
        background-size: cover;
        background-position: center center;
        background-attachment: fixed;

        .hello {
            margin: auto;

            color: ${colors.WHITE};
            text-align: center;
            text-transform: uppercase;
            padding: 30px 50px;

            ${mq.maxWidth('md')`
                padding: 20px 30px;
            `}

            h1 {
                font-size: 36px;
                font-weight: 600;
                white-space: nowrap;
                margin-bottom: 10px;

                ${mq.maxWidth('md')`
                    font-size:26px;
                `}
            }

            p {
                font-size: 20px;
                font-weight:300;

                ${mq.maxWidth('md')`
                    font-size: 16px;
                `}
            }
        }
    }

`;

export default GlobalStyles;
